import React,{useEffect, useState} from 'react'
import AccountInfoForm from './AccountInfoForm'
import AccountInfoShow from './AccountInfoShow'
import Spinner from '../../UI/Spinner'
const AccountInfoIndex = (props) => {
    //options-> pending , done , inReview
    const [status,setStatus] = useState('')
    useEffect(()=>{
        setStatus('pending')
    },[])
    if(status===''){
        return(
            <Spinner />
        );
    }
    if(status==='pending'){
        return(
            <AccountInfoForm {...props} />
        );
    }
    if(status==='done' || status==='inReview'){
        return(
            <AccountInfoShow />
        );
    }
    
}
export default AccountInfoIndex