import React, { useEffect,useState } from 'react'
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import BasicInfoIndex from './BasicInfoKyc/BasicInfoIndex'
import AccountInfoIndex from './AccountInfoKyc/AccountInfoIndex'
import {
    Card,
    Grid,
    IconButton,
    
} from '@material-ui/core'
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import TaxInfoIndex from './TaxInfoKyc/TaxInfoIndex';
const useStyles = makeStyles({
    root: {
       minWidth: 275,
    },
    title: {
      fontSize: 20,
      height: 50,
      padding: 10,
      paddingLeft: 55,
      color: 'white'
    },
    formHeadings: {
      margin: 20,
      marginBottom: 0
    }
  });
const KYCPanel = () => {
    const classes = useStyles()
    const [basicInfoStatus,setBasicInfoStatus] = useState('done')
    const [taxInfoStatus,setTaxInfoStatus] = useState('done')
    const [accountInfoStatus,setAccountInfoStatus] = useState('done')
    const [page,setPage] = useState('');
    useEffect(()=>{
        setPage('')
        setBasicInfoStatus('pending')
        setTaxInfoStatus('pending')
        setAccountInfoStatus('pending')
    },[])
    const changePage = (page) => {
      setPage(page)
    }
    if(page==='basicInfo'){
      return(
        <BasicInfoIndex changePage={changePage} />
      );
    }
    if(page==='taxInfo'){
      return(
        <TaxInfoIndex changePage={changePage} />
      )
    }
    if(page==='accountInfo'){
      return(
        <AccountInfoIndex changePage={changePage} />
      );
    }
    return(
        <Card>
            <Grid container style={{ backgroundColor: '#66bb6a',marginBottom:30}}  justify="space-between">  
            <Typography fullWidth className={classes.title} gutterBottom style={{ backgroundColor: '#66bb6a' }} inline variant="body1" align="left">Basic Info KYC,Status:{basicInfoStatus==='done'?'Done':'Pending'} </Typography>
            <Typography fullWidth className={classes.title} gutterBottom style={{ backgroundColor: '#66bb6a' }} inline variant="body1" align="right">
            <IconButton onClick={()=>changePage('basicInfo')} aria-label="expand row" size="small" >
                <ChevronRightIcon /> 
              </IconButton>
            </Typography>
            </Grid>
            <Grid container style={{ backgroundColor: '#66bb6a',marginTop:30,marginBottom:30,minHeight:'400%'}}  justify="space-between">  
            <Typography fullWidth className={classes.title} gutterBottom style={{ backgroundColor: '#66bb6a' }} inline variant="body1" align="left">Tax Info KYC,Status:{taxInfoStatus==='done'?'Done':'Pending'}  </Typography>
            <Typography fullWidth className={classes.title} gutterBottom style={{ backgroundColor: '#66bb6a' }} inline variant="body1" align="right">
            <IconButton onClick={()=>changePage('taxInfo')} aria-label="expand row" size="small" >
                <ChevronRightIcon /> 
              </IconButton>
            </Typography>
            </Grid>
            <Grid container style={{ backgroundColor: '#66bb6a',marginTop:30,minHeight:'400%'}}  justify="space-between">  
            <Typography fullWidth className={classes.title} gutterBottom style={{ backgroundColor: '#66bb6a' }} inline variant="body1" align="left">Account Info KYC,Status:{accountInfoStatus==='done'?'Done':'Pending'}  </Typography>
            <Typography fullWidth className={classes.title} gutterBottom style={{ backgroundColor: '#66bb6a' }} inline variant="body1" align="right">
            <IconButton onClick={()=>changePage('accountInfo')} aria-label="expand row" size="small" >
                <ChevronRightIcon /> 
              </IconButton>
            </Typography>
            </Grid>
          
        </Card>
    );
}
export default KYCPanel