import React,{useEffect, useState} from 'react'
import TaxInfoForm from './TaxInfoForm'
import TaxInfoShow from './TaxInfoShow'
import Spinner from '../../UI/Spinner'
const TaxInfoIndex = (props) => {
    //options-> pending , done , inReview
    const [status,setStatus] = useState('')
    useEffect(()=>{
        setStatus('pending')
    },[])
    if(status===''){
        return(
            <Spinner />
        );
    }
    if(status==='pending'){
        return(
            <TaxInfoForm {...props} />
        );
    }
    if(status==='done' || status==='inReview'){
        return(
            <TaxInfoShow />
        );
    }
    
}
export default TaxInfoIndex